.row{
  
    /* margin-top: 20px; */
    /* margin-top: 50px; */
    /* margin-left: 20px; */
    /* margin: 20px auto; */
    margin: 0 auto;
    color: #fff;
    position: relative;
    top: 20px;
    /* background-color: antiquewhite; */
}
.row h2{
  margin-left: 40px;
  font-size: 26px;
}
.posters{
  width: 95%;
    display: flex;
    justify-content: start;
    /* margin-left: -15px; */
    align-items: start;
    gap: 5px;
    padding: 10px 0;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: smooth;
    transition: display 1s ease;
    /* background-color: red; */
    /* transform: translateX(20px); */
    margin: 0 auto;
}
/* .posters:hover .pre,.posters:hover .next{
  display: block;
} */

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    /* max-height: 190px; */
    max-height: 200px;
    /* margin-right: 5px; */
    margin-right: 10px;
    /* border: px solid black; */
    transition: scale 0.3s ease,margin 0.2s ease;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}
.poster:hover{
    scale: 1.13;
    margin: 0 32px;
    border-radius: 5px;
    filter: brightness(70%);
    
}
/* .smallPoster:not(:hover){
} */
.smallPoster:hover{
    scale: 1.12;
    /* scale: 1.17; */
    margin: 0 30px;
    border-radius: 5px;
    filter: brightness(70%);
}
/* .smallPoster:hover .movieName{
    color: yellow;
} */
/* .poster:not(:hover) */
.smallPoster{
    margin-top: 10px;
    /* max-width: 330px; */
    max-height: 200px;
    margin-left: 10px;
    transition: all 0.2s ease;
    border-radius: 5px;
    cursor: pointer;

}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.845);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    /* padding: 50px; */
  }

.modalContent {
    position: relative;
    /* width: 90%; */
    height: 90%;
    aspect-ratio: 16/9;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 50px; */
  }


.close {
    position: absolute;
    top: -13px;
    right: -40px;
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  .video {
    width: 100%;
    height: 100%;
  }

  .movieName {
    /* background-color: black; */
    display: inline;
    position: relative;
    top: -50px;
    left: 30px;
    z-index: 5;
    font-weight: 600;
    font-size: 19px;
    /* color: black; */
    max-width: 340px;
  }
  .discription{
    width: 220px;
    height: 100px;
    overflow: hidden;
    position: absolute;
    z-index: 1;
  }

  .control{
    height: 78%;
    width: 100px;
    position: absolute;
    /* overflow: hidden; */
    top: 10%;
    z-index: 10;
    /* transform: translateY(-50%); */
    /* background-color: aqua; */
  }
  .pre_container{
    left: -20px;
    /* background :linear-gradient(to right,rgba(0,0,0,1) 20%,rgba(0,0,0,0.5) 60%, rgba(0, 0, 0, 0)); */
    cursor: pointer;
  }
  .next_container{
    right: 0;
    /* background :linear-gradient(to left,rgba(0,0,0,1) 20%,rgba(0,0,0,0.5) 60%, rgba(0, 0, 0, 0)); */
    cursor: pointer;
  }
  .pre{
    position: relative;
    /* top: 50%; */
    transform: translateY(-50%);
    width: 40px;
    left: 20px;
    /* display: none; */
    opacity: 0;
    top: 60%;
    transition: 0.3s opacity ease,0.3s top ease;
  }
  .next{
    position: relative;
    /* top: 50%; */
    transform: translateY(-50%);
    width: 40px;
    right: -60px;
    /* display: none; */
    opacity: 0;
    top: 60%;
    transition: 0.2s opacity ease,0.2s top ease;
  }

  .pre_container:hover .pre{
    /* display: block; */
    opacity: 1;
    top: 50%;
  }
  .next_container:hover .next{
    /* display: block; */
    opacity: 1;
    top: 50%;
  }