.FullContainer{
    background-color: black;
    height: 100vh;
    overflow: hidden;
}

nav .Not_found_link{
    color: white;
    position: fixed;
    left: 250px;
    top: 25px;
    text-decoration: none;
}
.NotFoundContainer{
    display: flex;
}
.NotFound_content,.NotFoundImage{
    width: 50%;
    /* height: 100vh; */
}
.NotFoundImage img{
    width: 75%;
    object-fit: cover;
    position: relative;
    top: -30px;
}
.NotFound_content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding: 40px;
}
.NotFound_content h1{
    font-size: 70px;
}
.NotFound_content p{
    font-size: 20px;
}
.NotFound_content button{
    width: 30%;
    background-color: black;
    border: 2px solid white;
    color: white;
    font-size: 16px;
    font-weight: 500;
    padding: 20px 30px;
    border-radius: 10px;
}