.movie_banner{
    /* width: 100%; */
    height: 100vh;
    /* display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center; */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

}
/* .movie_background{
    width: 100%; */
    /* backdrop-filter: brightness(0%);
} */
.movie_content{
    padding-top: 120px;
    height: 190px;
    padding-left: 25px;
    position: relative;
    /* top: 200px; */
    top: 150px;
    /* background-color: aqua; */
}
.movie_content h1{
    font-size: 3.6rem;
    font-weight: 700;
    /* padding-bottom: 0.3rem; */
}
.movie_content p{
    width: 55rem;
    line-height: 1.3;
    padding-top: 0.6rem;
    font-size: 1.24rem;
    max-width: 900px;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.737);
    display: flex;
    align-items: center;
    gap: 10px;
}
.movie_button{
    display: flex;
}

.Movie_btn{
    color: white;
    font-weight: 700;
    border: none;
    outline: none;
    border-radius: 5px;
    padding: 0.65rem 1.7rem;
    background-color: rgba(99, 98, 98, 0.662);
    cursor: pointer;
    margin: 8px 0 0px;
    margin-right: 1rem;
    font-size: 15px;
    display: flex;
    align-items: center;
    gap: 5px;
}
.Movie_btn:hover{
    /* color: black;
    border-color: #e6e6e6; */
    background-color: rgb(197, 197, 197);
    color: black;
}
.play{
    background-color: rgba(255, 255, 255, 0.567);
    color: black;
}
.play img{
    width: 25px;
}
.add img{
    width: 25px;
}
.movie_content .rating{
    width: 200px;
    font-size: 20px;
    font-weight: 400;
    display: flex;
    align-items: center;
    gap: 5px;
    /* background-color: red; */
}
.rating .imdb{
    /* width: 22px; */
    width: 35px;
}
.star{
    width: 25px;
}
.trending{
    height: 310px;
    padding-top: 20px;
    /* top: -500px; */
top: 200px;
/* left: -15px; */
    position: relative;
    background: linear-gradient(to top,rgba(0,0,0,1),rgba(0,0,0,0.5) ,rgba(0,0,0,0));
    /* background-color: aqua; */
}