.navbar{
    position: absolute;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 80px;
    /* background-color: #111; */
    /* background-color: #ffffff00; */
    /* backdrop-filter: blur(5px); */
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.mobile-navbar{
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    /* height: 80px; */
    /* background-color: rebeccapurple; */
    /* color: black; */
    display: none;
}
.logo{
    width: 110px;
    height: auto;
    position: relative;
    left: 40px;
    margin-right: 70px;
}
.avatar{
    width: 45px;
    position: relative;
    right: 20px;
    border-radius: 50%;
    object-fit: cover;
    background-position: center;
    /* object-position: center; */
}

.box{
    /* background-color: aliceblue; */
    /* width: 100%; */
    display: flex;
    align-items: center;
    gap: 40px;
}

/* .mobile-navbar_box{
    display: flex;
    flex-direction: column;
} */
nav.mobile_navbar_cnt {
    width: 50%;
    height: 100vh;
    padding: 40px;
    background-color: white;
    position: absolute;
    right: 0;
    top: 0px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.mobile_navbar_cnt .Link{
    /* color: white; */
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    color: black;
}
.menu{
    width: 35px;
    position: absolute;
    right: 10px;
    top: 20px;
    z-index: 10;
}

.nav{
    display: flex;
    align-items: center;
    gap: 40px;
}

.box a,.box_2 a{
    color: white;
    text-decoration: none;
    font-weight: 500;
}
.box .Link,.box_2 .Link{
    color: white;
    text-decoration: none;
    font-weight: 500;
}

.box_2{
    display: flex;
    gap: 40px;
    align-items: center;
}
.box_2 a{
    display: flex;
    align-items: center;
}

.signin_btn{
margin-right: 50px;
    border-radius: 3px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    line-height: 14px;
    font-weight: 500;
    font-style: normal;
    width: 75px;
    background-color: red;
    height: 31px;
    border: none;
    font-family: Netflix Sans, Helvetica Neue, Segoe UI, Roboto, Ubuntu, sans-serif;
}
.box_2 a img{
    width: 35px;
}

.search{
    border-radius: 3px;
    font-size: 1.05rem;
    font-weight: 400;
    color: white;
    line-height: 1.5;
    width: 260px;
    padding: 0.5rem 0.9rem;
    background-color: rgba(0, 0, 0, 0.69);
    border: 1px solid gray;
    margin-right: 5px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.lang_row{
    display: flex;
    align-items: center;
    gap: 2px;
}
.search_icon{
    /* margin-top: 10px; */
    position: relative;
    top: 10px;
    width: 35px;
}

@media (max-width: 910px) {
    .navbar {
      /* font-size: 3rem; Larger font size for wider screens */
      display: none;
    }
    .mobile-navbar{
        display: flex;
        flex-direction: column
    }
    .logo{
        margin-top: 20px;
        height: 40px;
    }
  }

  @media(max-width:760px){
    nav.mobile_navbar_cnt{
        width: 100%;
    }
  }