.MyList_contaniner{
    width: 100%;
    height: 100vh;
    padding: 120px 40px;
    /* background-color: antiquewhite; */
    
}
.MovieList_Container{
    margin-top: 30px;
}
.list{
    padding: 20px 0;
    display: grid;
    /* grid-template-columns: repeat(4,1fr); */
    /* grid-template-columns: repeat(10,1fr); */
    grid-template-columns: repeat(7,1fr);
    gap: 40px 20px;
}
/* .Series_cntr{
    margin-top: 40px;
} */

.List_image{
    width: 100%;
    border-radius: 3px;
    transition: scale 0.2s ease;
    cursor: pointer;
}
.List_image:hover{
    scale: 1.07;
}
.List_name{
    font-size: 20px;
    font-weight: 500;
}
/* .line{
    width: 100%;
