    border: none;
    margin-top: 40px;
} */
.Empty{
    width: 300px;
    /* background-color: rgb(46, 81, 81); */
    font-size: 25px;
    font-weight: 400;
}
.List_btn{
    background-color: red;
    /* background-color: #646363; */
    color: white;
    font-size: 18px;
    font-weight: 600;
    padding: 10px 30px;
    border: none;
    border-radius: 3px;
    margin-right: 10px;
    transition: scale 0.3s ease;
}
.List_btn:hover{
    background-color: rgb(183, 5, 5);
}
.List_btn:active{
    scale: 1.05;
}