.logout{
    padding: 15px 31px;
    position: absolute;
    top: 67px;
    right: 15px;
    background-color: rgba(0, 0, 0, 0.838);
    /* width: 150px; */
    display: flex;
    flex-direction: column;
    /* justify-content: end; */
    align-items: end;
}
.lang_box{
    /* padding: 15px 20px; */
    width: 120px;
    position: absolute;
    top: 67px;
    right: 168px;
    background-color: rgba(0, 0, 0, 0.838);
    /* width: 150px; */
    display: flex;
    flex-direction: column;
    /* justify-content: end; */
    align-items: start;
    /* gap: 15px; */
}
.lang{
    padding: 0 20px;
    height: 40px;
    width: 100%;
    display: inline;
    background-color: transparent;
    color: white;
    border: none;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    align-items: center;
    /* gap: 15px; */
    transition: scale 0.5s ease;
}
.logout button{
    display: inline;
    background-color: transparent;
    color: white;
    border: none;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 4px;
    transition: scale 0.5s ease;
}
.logout:hover,
.lang:hover {
    background-color: rgb(88, 87, 87);
}
/* .lang_box */
.logout:active{
    scale: 1.05;
}
.log_out{
    width: 20px;
}